.campaign-result-popup-wrapper {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 9999;
  background-color: rgba($fontdark, .9);
  display: flex;
  justify-content: center;
  align-items: center; }

.campaign-result-popup {
  max-width: rem(850);
  @include roboto;
  border-radius: 5px; }

.popup__header {
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  padding: rem(25);
  border-radius: 5px 5px 0 0; }

.header-links {
  margin-left: 8.3vw;
  margin-right: -40px;
  display: flex;
  align-items: center; }

.header-button {
  color: #fff;
  display: inline-block;
  width: rem(35);
  height: rem(35);
  border-radius: 50%;
  background-color: $fontlight;
  font-family: 'Traverse';
  font-style: normal;
  font-weight: normal;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  margin-left: rem(10);
  position: relative;
  font-size: 1vw; }

a.save-list {
  color: $savelink; }

.menu-link:before {
  content: '\0073';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff; }

.share-icon:before {
  content: '\0033';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff; }

.brand__campaign {
  display: flex;
  align-items: center; }

.brand-name {
  margin-left: rem(30);
  font-size: rem(23); }

.campaign-word {
  color: $fontlight; }

.estimate-word {
  color: $orange; }

.campaign-estimate {
  //height: 8vw
  background-color: #d5543a;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding-top: rem(25); }

.campaing-title {
  color: #fff;
  font-size: rem(36);
  text-transform: uppercase;
  @include robotoLight;
  padding-bottom: rem(35); }

.campaign-image {
  margin-bottom: rem(40); }

.campaign-content {
  background-color: #fff;
  padding: rem(30) rem(40);

  p {
    padding-bottom: rem(30); }

  .influencers-number {
    @include robotoBlack;
    font-size: rem(18);
    padding-bottom: rem(15); } }


.carousel__influencer {
  width: 9.7vw;
  .carousel__influencer__image {
    width: 3.8vw;
    height: 3.8vw;
    margin-top: -1.945vw;
    margin-bottom: 0.75vw; }

  .carousel__influencer__top {
    height: 4.15vw; }

  .carousel__influencer__name {
    font-size: 0.55vw; } }

.submit-wrapper {
  display: flex;
  justify-content: flex-end;

  .button_green {
    background-color: #08ddc1;
    font-size: rem(14); } }
